
.projects {
    display: grid;
    margin: 10px;
}

.app {
    padding: 10px;
    min-height: 100vh;
    align-items: center;
}

.star-contain {
    position: relative;
    z-index: 0;
    height: 100%;
}

.tabs {
    position: relative;
    z-index: 0;
    min-height: 100vh;
}



.links {
    display: flex;
    
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 2rem;
}

.mode-toggle {
    background-color: transparent;
    border: none;
}
.darkmode-img {
    transition: transform 0.3s ease;
}

.light-mode:hover {
    content: url('/public/dark_mode1.png'); 
    transform: scale(1.2);
}
  
.dark-mode:hover {
    content: url('/public/light_mode2.png'); 
    transform: scale(1.2);
}

.footer-links {
    display: flex;
    justify-content: center;
    font-size: 2rem;
}

.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.header-links{
    display: flex;
    
}
@media (max-width: 500px) {
    .header-links {
        display: none;
    }
}