.about-me {
    height: 100%;
    z-index: 0;
    display: flex;
    justify-content: center;

}


.title {
    font-size: 4rem;
    font-weight: bold;
    z-index: 0;
}

.intro {
    width: 45%;
}

.intro p {
    line-height: 2;
}

.description {
    font-size: 2rem;
}

.portrait {
    border-radius: 50%;
    margin-left: 4rem;
    margin-top: 4rem;
    border: 5px solid rgb(210, 56, 210);
    border-width: 2px;
}
@media (max-width: 768px) {
    .about-me {
        flex-direction: column;
        width: 100%;

    }
    .intro {
        width: 100%;
  }
  .portrait-container {
    display: flex;
    justify-content: center;
  }
  .portrait {
    margin: 0;
  }

  }