.nav-bar {
    display: flex;
    z-index: 2;
}

.nav-bar-button {
    padding: 0.5rem 1rem 0.5rem 1rem;
    border-radius: 1rem;
    background-color: transparent;
    border: none;
    transition: transform 0.3s ease, background-color 0.3s ease;
    color: inherit; 
    font-size: inherit;
}

.nav-bar-button-home {
    padding: 0.5rem 1rem 0.5rem 1rem;
    border-radius: 1rem;
    background-color: transparent;
    border: none;
    transition: transform 0.3s ease, background-color 0.3s ease;
    color: inherit; 
    font-size: inherit;
    font-weight: bold
}

.nav-bar-button:hover {
    background-color: #e2beff;
    transform: scale(0.12rem);
}

.dropdown {
    height: 30px;
    width: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.5rem;
}
.dropdown-btn{
    height: 3rem;
    width: 3rem;
}

.dropdown-menu-dark {
    background-color: #24262c;
    border-radius: 1rem;
    border: 1px solid #A9A9A9;
    width: 10rem;
    height: 7rem;
    font-size: 1rem;
    flex-direction: column;
    display: flex;
}

.dropdown-menu-light {
    background-color: #fcfae3;
    border-radius: 1rem;
    border: 1px solid #A9A9A9;
    width: 10rem;
    height: 7rem;
    font-size: 1rem;
    flex-direction: column;
    display: flex;
}