.link-container {
    padding: 5px 10px 5px 10px;
    margin-right: 5px;
    border-radius: 10px;
    text-decoration: none;
    align-items: flex-end;
    transition: transform 0.3s ease, background-color 0.3s ease;
}

.link-container:hover {
    background-color: #e2beff;
    transform: scale(1.2);
}

.link {
    text-decoration: none;
    display: flex;
    align-items: flex-end;
    color: inherit; 
}

.link-img-dark {
    height: 20px;
    filter: invert(100%)
}
.link-img-light {
    height: 20px;

}